.App {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
  /* text-align: center; */
  background-color: #444;
  direction: rtl; }
  .App.DarkTheme {
    background-color: black; }

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none; }

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white; }

.App-link {
  color: #61dafb; }

button {
  transition: background-color 0.3s, top 0.5s;
  border: none;
  border-width: 0;
  background-color: transparent;
  color: #ddd;
  pointer-events: all;
  text-align: center; }
  button:hover {
    color: white !important;
    background: #666; }
  button * {
    pointer-events: none; }

.ButtonsBar {
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  align-items: stretch;
  padding: 5px 4px;
  flex-grow: 1;
  min-height: 40px; }
  .ButtonsBar button {
    margin: 0 2px;
    padding: 0;
    border-radius: 6px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    flex-grow: 1;
    font-size: 15px;
    background: #444; }
    .ButtonsBar button span {
      line-height: 16px; }
    .ButtonsBar button:hover {
      color: white;
      background: #666; }

.ActionsBar {
  display: flex;
  justify-content: flex-start;
  align-items: stretch; }
  .ActionsBar > span {
    line-height: 30px;
    padding: 0 3px 0 0; }
    [dir="rtl"] .ActionsBar > span {
      padding: 0 0 0 3px; }
    .ActionsBar > span:first-child::after {
      content: ":"; }
  .ActionsBar button {
    background-color: #444; }
    li .ActionsBar button {
      background-color: rgba(255, 255, 255, 0.2);
      width: inherit;
      margin: 0 2px 2px 0; }

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg); }
  to {
    transform: rotate(360deg); } }

hr {
  border-style: solid;
  border-width: 1px 0;
  border-color: #222 #888 #888 #222; }

.iBlock {
  display: inline-block;
  margin: 0 5px; }

.flipHorizontal {
  transform: scaleX(-1); }

.VerseInfo {
  float: left;
  display: flex;
  min-width: 60px;
  flex-direction: column; }
  .PopupBody .VerseInfo {
    margin: 0 10px 10px 0; }
  .VerseInfo .VerseInfoList {
    font-size: 13px;
    display: flex;
    flex-direction: column;
    align-items: center; }
  .VerseInfo button {
    min-height: 25px; }

.VerseText {
  margin: 0;
  line-height: 22px;
  color: #cfcf00;
  text-overflow: ellipsis;
  overflow: hidden; }

.circle-background,
.circle-progress {
  fill: none; }

.circle-background {
  stroke: #444; }

.circle-progress {
  stroke: yellowgreen;
  stroke-linecap: round;
  stroke-linejoin: round; }

.circle-text {
  font-size: 10px;
  font-weight: bold;
  fill: yellowgreen;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif; }

.CommandButton {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 25px !important;
  height: 50px !important;
  min-width: 50px;
  padding: 0;
  pointer-events: fill; }
  .CommandButton span svg {
    width: 50px; }
  .CommandButton span img {
    width: 40px; }
  .CommandButton .CommandLabel {
    flex-grow: 1; }
  .Sidebar .CommandButton {
    justify-content: initial; }

.NoHifz {
  background-color: rgba(0, 117, 195, 0.219);
  border-color: #006db6; }

.GoodHifz {
  background-color: rgba(0, 128, 0, 0.15);
  border-color: #005a00 !important; }

.FairHifz {
  background-color: rgba(189, 145, 0, 0.2);
  border-color: #795600 !important; }

.WeakHifz {
  background-color: rgba(177, 0, 0, 0.15);
  border-color: brown !important; }

.HifzRangeRow {
  margin-bottom: 5px; }
  .HifzRangeRow > button:hover {
    background-color: transparent !important; }
  .HifzRangeRow .AgeInfo {
    float: right; }
    [dir="rtl"] .HifzRangeRow .AgeInfo {
      float: left; }
  .HifzRangeRow .RangeInfo {
    font-weight: bold; }
  .HifzRangeRow .ActionsBar {
    position: absolute;
    bottom: 0; }
  .HifzRangeRow .RangeText {
    height: 30px;
    display: flex;
    align-items: center; }

.ContentFrame {
  padding: 15px 10px; }

.FootNote {
  border-top: solid 1px white;
  padding-top: 5px;
  margin-top: 15px;
  font-size: smaller;
  clear: both; }

.HiddenScroller {
  overflow: -moz-scrollbars-none;
  -ms-overflow-style: none;
  scrollbar-width: none; }
  .HiddenScroller::-webkit-scrollbar {
    width: 0 !important; }

.SuraHifzChart {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  border: solid 1px #444;
  box-sizing: border-box;
  border-radius: 5px;
  pointer-events: none; }
  .HifzRangeRow .SuraHifzChart {
    height: 46%; }
  .SuraHifzChart .SuraRange {
    position: absolute;
    height: 100%;
    border-style: solid;
    border-width: 1px;
    box-sizing: border-box;
    border-radius: 4px; }
  .SuraHifzChart .PageThumb {
    position: absolute;
    height: 100%;
    border-style: solid;
    border-color: rgba(170, 170, 170, 0.2);
    border-width: 0 0 0 1px;
    background-position-x: 2px;
    box-sizing: border-box; }
    .SuraHifzChart .PageThumb.ActivePage {
      background-color: rgba(170, 170, 170, 0.2); }

.UserImage {
  width: 50px;
  height: 50px;
  border-radius: 25px;
  margin: 10px; }
  .Sidebar .UserImage {
    width: 30px;
    height: 30px;
    border-radius: 15px; }

.SuraIndexCell {
  margin: 2px 0; }
  .SuraIndexCell button {
    border-radius: 5px; }

.recharts-wrapper {
  direction: ltr;
  text-align: center;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 12px;
  margin: 10px 20px; }

.VCentered {
  display: flex;
  flex-direction: column;
  justify-content: center; }

.RadioGroup label {
  display: flex;
  align-items: center;
  height: 30px; }
  .RadioGroup label span {
    display: inline-block; }
  .RadioGroup label input {
    margin: 0 10px; }
  .RadioGroup label:hover {
    background-color: #ffffff15; }

.ToastMessage {
  z-index: 1;
  position: fixed;
  max-width: 200px;
  border-radius: 10px;
  background-color: #000000a1;
  color: yellow;
  display: none;
  align-items: center;
  padding: 20px;
  left: 50%;
  top: 50%;
  transform: translateY(-50%) translateX(-50%);
  border: solid 3px rgba(255, 255, 255, 0.493);
  cursor: default; }
  .ToastMessage.ShowToast {
    display: flex;
    animation: ShowToast 0.5s; }
  .ToastMessage.HideToast {
    animation: HideToast 0.5s;
    display: flex; }

@keyframes ShowToast {
  from {
    top: 100%; }
  to {
    top: 50%; } }

@keyframes HideToast {
  from {
    top: 50%; }
  to {
    top: 100%;
    transform: translateX(-50%) translateY(0); } }
